import React from 'react';
import { fetchNew, setLastWeekChange, getAve } from '../apiServices';

class Realtime extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            date: new Date(),
            chartName: '',
            usdRate: 0,
            lastUpdate: '',
            lastWeek: [],
            changeLastWeek: 0,
            changeLastWeekPercent: 0
        };
    }

    componentDidMount() {
        fetchNew(this)
    }

    render() {

        let arrowHtml;
        if (this.state.changeLastWeek >= 0) {
            arrowHtml = <i className="fas fa-arrow-up"></i>;
        } else {
            arrowHtml = <i className="fas fa-arrow-down"></i>;
        }


        return (
            <div>
                <h1>{this.state.chartName} real-time price</h1>
                <button className='btn btn-lg' onClick={this.fetchNew}>Refresh</button>

                <div id='rate-div' className='jumbotron'>
                    <h1 className="display-1">$ {this.state.usdRate}</h1>
                    <p>Bitcoin Price</p>
                </div>
                <div id='compare-div' className='jumbotron'>
                    <div className="row">
                        <div className="col">
                            <h1>$ {this.state.changeLastWeek} {arrowHtml}</h1>
                            <p>Since Last Week, USD</p>
                        </div>
                        <div className="col">
                            <h1>{this.state.changeLastWeekPercent}</h1>
                            <p>Since Last Week, %</p>
                        </div>
                    </div>
                </div>
                <div id='update-div' className='jumbotron'>
                    <h3 className='display-3'>price updated at {this.state.lastUpdate}</h3>
                </div>
            </div>
        );
    }


}

export default Realtime;


